
body{
  margin: 0;
}
.float-l {
  float: left;
}
.float-r {
  float: right;
}
.margin-r {
  margin-right: 2em;
}
.margin-l {
  margin-left: 2em;
}
.clear {
  clear: both;
}
article {
  padding: 0 2em 2em;
  overflow: hidden;
}
section {
  overflow: hidden;
}














.letter{
  display: inline-block;
  font-weight: 900;
  font-size: 100px;
  position: relative;
  color: #5f3d8a;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 400px;
          perspective: 400px;
  line-height: 1;
  z-index: 1;
}
.letter:before, .letter:after{
  position:absolute;
  content: attr(data-letter);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  top:0;
  left:0;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.letter, .letter:before, .letter:after{
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.letter:before{
  color: #fff;
  text-shadow:
    -1px 0px 1px rgba(255,255,255,.8),
    1px 0px 1px rgba(0,0,0,.8);
  z-index: 3;
  -webkit-transform:
    rotateX(0deg)
    rotateY(-15deg)
    rotateZ(0deg);
          transform:
    rotateX(0deg)
    rotateY(-15deg)
    rotateZ(0deg);
}
.letter:after{
  color: rgba(0,0,0,.11);
  z-index:2;
  -webkit-transform:
    scale(1.08,1)
    rotateX(0deg)
    rotateY(0deg)
    rotateZ(0deg)
    skew(0deg,1deg);
          transform:
    scale(1.08,1)
    rotateX(0deg)
    rotateY(0deg)
    rotateZ(0deg)
    skew(0deg,1deg);
  width: 100%;
  height: 100%;
}
.letter:hover:before{
  color: #fafafa;
  -webkit-transform:
    rotateX(0deg)
    rotateY(-40deg)
    rotateZ(0deg);
          transform:
    rotateX(0deg)
    rotateY(-40deg)
    rotateZ(0deg);
}
.letter:hover:after{
  -webkit-transform:
    scale(1.08,1)
    rotateX(0deg)
    rotateY(40deg)
    rotateZ(0deg)
    skew(0deg,22deg);
          transform:
    scale(1.08,1)
    rotateX(0deg)
    rotateY(40deg)
    rotateZ(0deg)
    skew(0deg,22deg);
}
